import req from '../../utils/req.js';
export default {
	data() {
		return {
			goodObj:{}
		}
	},
	async mounted(){
		const goodId = this.$mp.query.goodId;
		let result = await req('/getGoodDetail',{goodId});
		this.goodObj = result;
	}
}
